.cform{
    @media screen and (max-width: 1439px){
        margin-bottom: 60px;
    }

    @include tablet{
        max-width: 400px;
        @include centeredMarg;
    }
    &__title{
        font-family: 'Comic Neue', cursive;
        font-weight: 700;
        font-size: 24px;
        line-height: 30px;
        color: $title-color;

        margin-bottom: 44px;
    }

    &__field{
        display: block;
        position: relative;

        font-family: 'Montserrat', sans-serif;
        font-size: 14px;
        line-height: 20px;

        & + & {
            margin-top: 44px;
        }

    }

    &__label{
        position: absolute;
        top: 0;
        left: 0;

        padding-bottom: 4px;

        transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

        .cform__field:focus-within > &{
            transform: translateY(-20px);
        }
    }

    &__input{
        width: 100%;
        border: none;
        border-bottom: 1px solid $title-color;

        &:not(:placeholder-shown) + .cform__label{
            transform: translateY(-20px);
        }
    }

    &__textarea{
        resize: none;

        width: 100%;
        height: 199px;

        border: 1px solid #487996;
        border-radius: 50px 0px;

        &::placeholder{
            padding: 22px 20px;
        }
    }

    &__textarea-label{
        margin-bottom: 4px;
    }

    &__text{
        color: $placeholder-color;
    }

    &__button{
        display: block;
        margin-top: 40px;
        @extend %btn;
        @include centeredMarg();

        @include tablet{
            margin-left: auto;
            margin-right: 0;
            min-width: 177px;
        }
    }
}